* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

:root {
	--mainColor: #222;
	--mainColorLight: #5767aa;
	--secondaryColor: #30D5D5;
	--textColor: #eee;
    --mainImage: linear-gradient(#222, #111);
}

header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	width: 100%;
	padding: 0 2rem;
	background-color: var(--mainColor);
	color: var(--textColor);
    background-image: var(--mainImage);
	position: fixed;
}
nav div {
	margin: 0 1rem;
	color: var(--textColor);
	background-color: rgba(0, 0, 0, 0);
	text-decoration: none;
}

nav div:hover {
	color: var(--secondaryColor);
}

nav a {
	margin: 0 1rem;
	color: var(--textColor);
	text-decoration: none;
}

nav a:hover {
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}
  
.logo2 {
	border-radius: 200px;
	height: 60px;
	width: 60px;
	justify-content: center;
}

header div,
nav {
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 780px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;
	}

	header nav {
		position: fixed;
		top: -150vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
	}

	header .responsive_nav {
		transform: translateY(150vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}

/* code used from https://github.com/Index-Zero-0/Responsive-navbar-ReactJS */