body {
  background-color: #191919;
}

int1 {
  font-size: 60pt;
  float: left;
  margin-top: 100px;
  margin-left: 5%;
  margin-right: 50px;
}

int2 {
  float: right;
  margin-top: 100px;
  margin-left: 10px;
  margin-right: 15%;
}

int2 a2 {
  float: right;
}

int2 .logo {
  border-radius: 200px;
  height: 320px;
  width: 320px;
  justify-content: center;
}

int2 a1 {
  float: right;
}

int3 {
  font-size: 50pt;
  float: left;
  padding: 10%;
  padding-bottom: 60px;
}

int4 {
  font-size: 20pt;
  float: right;
  align-self: center;
  padding: 2%;
}

@media only screen and (max-width: 780px) {
  int1 {
    font-size: 45px;
  }

  int2 .logo{
    height: 250px;
    width: 250px;
  }

  int3 {
    font-size: 30pt;
  }

  int4 {
    font-size: 14pt;
  }

}